// Declaraciones
@use 'variables' as v;
// Directivas de SASS

// Media queries
@mixin telefono {
    @media(min-width: 480px){
        @content;
    }
}

@mixin tablet {
    @media(min-width: 768px){
        @content;
    }
}

@mixin desktop {
    @media(min-width: 992px){
        @content;
    }
}

@mixin desktopXL {
    @media(min-width: 1200px){
        @content;
    }
}

// Grid

@mixin grid($columnas, $gap){
    display: grid;
    grid-template-columns: repeat($columnas,1fr);
    gap: $gap;
}

// Recetear valores de lista

@mixin resetear-lista {
    margin:0;
    padding: 0;
    list-style: none;
}