// Declaraciones

@use 'base/mixins' as m;
@use 'base/variables' as v;

// Body
.lineup{
    background-color: v.$rosa;
    padding: 5rem 0;

    h3, p{
        text-align: center;
        color: v.$blanco;
    }

    .dia{
        text-transform: uppercase;
        margin: 5rem 0;
    }
}

.escenarios-contenedor{
    @include m.tablet{
        @include m.grid(2, 5rem);
    }

    p{
        color: v.$negro;
    }
}

.escenario{
    padding-top: 2rem;
    padding-bottom: 2rem;
    p{
        &:after{
            content: '';
            display: block;
            width: 20rem;
            height: 10rem;
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center center ;
            margin: 2rem auto 0 auto;
        }
    }
}
.edm .nombre-escenario:after{
    background-image:url(../../../build/img/icono_edm.png);
}

.rock .nombre-escenario:after{
    background-image:url(../../../build/img/icono_rock.png);
}
.calendario{
    // Utilizamos mixin para recetear lista
    @include m.resetear-lista;
    li{
        padding: 2.5rem;
        font-weight: bold;
        span{
            font-weight: 400;
        }
    }
}
.bg-amarillo{
    background-color: v.$amarillo;

    //Odd para que lo haga con los impares
    li:nth-child(odd){
        background-color: darken(v.$amarillo, 10%);
    }
}

.bg-verde{
    background-color: v.$verde;

    //Odd para que lo haga con los impares
    li:nth-child(odd){
        background-color: darken(v.$verde, 10%);
    }
}